import React from 'react'
import axios from 'axios'
import { updateProduct } from '../../../services/api/sellerApi'
import { useState, useRef } from 'react'
import {
  CButton,
  CCol,
  CForm,
  CFormInput,
  CInputGroup,
  CFormLabel,
  CFormTextarea,
  CInputGroupText,
  CImage,
  CRow
} from '@coreui/react'
import { useToast } from '../../../contexts/toast'

const FormDetail = (recvData) => {
  const { error, warn, info, success } = useToast()
  const inputFile = useRef(null)
  const [data, setData] = useState(recvData.data)
  const [sizes, setSizes] = useState(data.sizes)
  const [imageUrls, setImageUrls] = useState(data.images)

  const handleSubmit = (event) => {
    event.preventDefault()
    delete data.views
    delete data._id
    delete data.rating
    delete data.createdAt
    delete data.sold
    delete data.updatedAt
    delete data.__v
    var idProduct
    var idShop
    data.images = imageUrls

    //console.log(data);
    if (idProduct == null) {
      idProduct = data.idProduct
      delete data.idProduct
    }
    if (idShop == null) {
      idShop = data.shop._id
      delete data.shop
    }
    // console.log(data);
    updateProduct(idShop, idProduct, data)
      .then((respone) => {
        if (respone.data.success == true) {
          success(respone.data.message)
          window.location.reload(false)
        } else {
          error(respone.data.message)
        }
      })
      .catch((err) => {
        error(err.response.data.message)
      })
  }
  const handleChange = (e) => setData({ ...data, [e.target.name]: e.target.value })

  const uploadImage = (image) => {
    // To mt form data cha d liu gi ln
    const formData = new FormData()
    // Hnh nh cn upload
    formData.append('file', image)
    // Tn preset va to  bc 1
    formData.append('upload_preset', 'new_preset')
    // Ti nh ln cloudinary
    // API: https://api.cloudinary.com/v1_1/{Cloudinary-Name}/image/upload
    axios
      .post('https://api.cloudinary.com/v1_1/accomerce/image/upload', formData)
      .then((response) => {
        setImageUrls([...imageUrls, response.data.secure_url])
      })
      .catch((err) => console.error(err))
  }
  // handle input change
  const handleInputChange = (e, index) => {
    const { name, value } = e.target
    const list = [...sizes]
    list[index][name] = value
    setSizes(list)
    data.sizes = sizes
  }

  // handle click event of the Remove button
  const handleRemoveClick = (index) => {
    const list = [...sizes]
    list.splice(index, 1)
    setSizes(list)
    data.sizes.splice(index, 1)
  }
  //console.log(data);
  // handle click event of the Add button
  const handleAddClick = () => {
    if (sizes[sizes.length - 1].name == '' || sizes[sizes.length - 1].name == '') {
      warn('Vui lng in  thng tin v kch thc trc khi thm mi')
    } else {
      setSizes([...sizes, { name: '', numberInStock: '' }])
    }
  }
  const deleteFile = (e) => {
    const s = imageUrls.filter((image, index) => index !== e)
    setImageUrls(s)
    //console.log(s);
  }
  const onButtonClick = () => {
    inputFile.current.click()
  }
  return (
    <div>
      <CForm className="row g-3">
        <div className="mb-3">
          <CFormLabel htmlFor="exampleFormControlInput1">Tn sn phm</CFormLabel>
          <CFormInput
            type="text"
            id="exampleFormControlInput1"
            name="name"
            placeholder="Nhp tn sn phm"
            onChange={(e) => handleChange(e)}
            value={data.name}
          />
        </div>
        <div className="mb-3">
          <CFormLabel htmlFor="exampleFormControlInput1">Loi sn phm</CFormLabel>
          <CFormInput
            type="text"
            id="exampleFormControlInput1"
            name="category"
            placeholder="Nhp loi mt hng m bn mun  xut"
            onChange={(e) => handleChange(e)}
            value={data.category}
          />
        </div>
        <div className="mb-3">
          <CFormLabel htmlFor="exampleFormControlTextarea1">M t sn phm</CFormLabel>
          <CFormTextarea
            rows="3"
            name="description"
            onChange={(e) => handleChange(e)}
            value={data.description}
          ></CFormTextarea>
        </div>
        <CCol xs={12}>
          <CFormLabel htmlFor="inputAddress">Size</CFormLabel>
          {sizes.map((size, i) => {
            return (
              <div className="box" key={i} id="inputSize">
                <CRow>
                  <CCol xs>
                    <CFormInput
                      name="name"
                      value={size.name}
                      placeholder="Nhp tn size"
                      onChange={(e) => handleInputChange(e, i)}
                    />
                  </CCol>
                  <CCol xs>
                    <CFormInput
                      name="numberInStock"
                      value={size.numberInStock}
                      placeholder="Nhp s lng"
                      onChange={(e) => handleInputChange(e, i)}
                      type="number"
                    />
                  </CCol>
                </CRow>
                {sizes.length - 1 === i && (
                  <CButton color="dark" shape="rounded-pill" onClick={handleAddClick} size="sm">
                    Thm
                  </CButton>
                )}
                {sizes.length !== 1 && (
                  <CButton color="dark" shape="rounded-pill" size="sm" onClick={() => handleRemoveClick(i)}>
                    Xa
                  </CButton>
                )}
              </div>
            )
          })}
        </CCol>
        <CCol xs={12}>
          <CFormInput
            type="file"
            onChange={(e) => {
              uploadImage(e.target.files[0])
            }}
            ref={inputFile}
            style={{ display: 'none' }}
          />
          <CButton onClick={onButtonClick} color="secondary" shape="rounded-pill">
            Thm nh
          </CButton>
        </CCol>
        <CCol xs id="imageShow">
          {imageUrls &&
            imageUrls.map((imageUrl, index) => {
              return (
                <CImage fluid src={imageUrl} key={index} width={150} height={150} onClick={() => deleteFile(index)} />
              )
            })}
        </CCol>
        <div className="mb-3">
          <CRow>
            <CCol xs>
              <CFormLabel htmlFor="exampleFormControlInput1">Gi bn</CFormLabel>
              <CInputGroup>
                <CFormInput type="number" name="price" onChange={(e) => handleChange(e)} value={data.price} />
                <CInputGroupText>VN</CInputGroupText>
              </CInputGroup>
            </CCol>
            <CCol xs>
              <CFormLabel htmlFor="exampleFormControlInput1">Gi gc</CFormLabel>
              <CInputGroup>
                <CFormInput
                  type="number"
                  id="exampleFormControlInput1"
                  name="originalPrice"
                  placeholder="Nhp gi gc sn phm ca bn"
                  onChange={(e) => handleChange(e)}
                  value={data.originalPrice}
                />
                <CInputGroupText>VN</CInputGroupText>
              </CInputGroup>
            </CCol>
          </CRow>
        </div>
        <CCol xs={12}>
          <CButton onClick={handleSubmit} type="submit">
            Lu
          </CButton>
        </CCol>
      </CForm>
    </div>
  )
}
export default FormDetail
