import React from 'react'

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'))

const AddProduct = React.lazy(() => import('./views/products/AddProduct'))
const ChangeProduct = React.lazy(() => import('./views/products/ChangeProduct'))
const DeleteProduct = React.lazy(() => import('./views/products/DeleteProduct'))

const TableShop = React.lazy(() => import('./views/shops/TableShop'))
const AddShop = React.lazy(() => import('./views/shops/AddShop'))

const routes = [
  { path: '/seller', name: 'Tng quan', exact: true, component: Dashboard },
  { path: '/seller/add-product', name: 'Thm sn phm', component: AddProduct },
  { path: '/seller/change-product', name: 'Sa sn phm', component: ChangeProduct },
  { path: '/seller/delete-product', name: 'Xa sn phm', component: DeleteProduct },
  { path: '/seller/shops', name: 'Danh sch ca hng', component: TableShop },
  { path: '/seller/register-shop', name: 'Thm ca hng', component: AddShop }
]

export default routes
